.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: #263238;
}

.header {
  display: flex;
  width: 100%;
  margin-bottom: 10px;
  border-bottom: 1px solid #cfd8dc;
  position: fixed;
  top:0;
  left:0;
  width: 100%;
  background-color: #fff;
}

.inside-header {
  width: 60%;
  display: flex;
  margin-left: auto;
  margin-right: auto;
}

.inside-header img{
  width: 125px;
  border-radius: 50%;
  border: 8px solid #37474f;
}

.header-image {
  border-right: 1px solid #cfd8dc;
  padding-right: 20px;
  padding-bottom: 20px;
  padding-top: 30px;
}

.header-breakdown {
  padding-top: 40px;
  padding-left: 20px;
  padding-bottom: 30px;
}

.header-image img {
  margin-left: 20px;
}

.summary {
  padding-top: 215px;
}

.breakdown{
    width: 60%;
    margin-bottom: 10px;
    margin-left: auto;
    margin-right: auto;
}

.experience {
  margin-bottom: 10px;
}

.bottom-line {
  padding-bottom: 10px;
  border-bottom: 1px solid #cfd8dc;
}

footer {
  color: #999;
  text-align: center;
  padding: 100px 0 50px 0;
  background: #080808;
}

h1 {
  font-size: 2em;
  font-weight: 400;
  margin: 0;
}

/* responsive */
@media only screen
and (max-width : 720px) {

  header {
    padding: 30px 30px 50px 30px;
  }

  .angled {
    padding: 100px 0;
  }

  footer {
    padding: 65px 0 50px 0;
  }

  h1 {
    font-size: 1.5em;
  }

}
